.beer-card{
  width:320px;
  height: 650px;
  border-radius:10px;
  overflow: hidden;

  &__img{
      height: 500px;
      width: auto;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      position: relative;
      color:#fff;
      padding-top: 40px;
  }


  /*&__info{
    padding:15px;
    .info-description{
        background-color: lightgrey;
        overflow: scroll;
        //white-space: nowrap;
        width: 350px;
        height: 400px;
    }
  }*/

  &__alcohol{
    border: none;
      background-color:#ECECEC;
      color: black;
      border-radius: 5px;
      padding: 9px 12px;
      margin-top: 10px;
      font-weight: bold;
      cursor: pointer;
  }
}



.bg-color{
  padding: 30px 40px 80px 40px;
  position: relative;
  width: 100%;
  color: #fff;
  height: 420px;
  .beer-card-name{
    position: absolute;
    background-color: lightgrey;
    top: 0;
    text-align: center;
    left: 0;
    right: 0;
    width: 100%;
    text-align: center;
    padding: 20px 0 40px 0;
   
  }
  .beer-card-tagline{
    position: absolute;
    bottom: 14px;
    font-size: 5px;
    text-align: center;
    left: 0;
    right: 0;
    width: 100%;
  }

    &--yellow{
      background-color: #f8cB00;
    }
    &--red{
      background-color: #EF3740;
    }

}

.info__container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top:20px;
  
}
